<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 提示信息 -->
            <a-form-model-item label="设备家族名称" prop="resourceFamilyName">
              <a-input
                v-model:value="entity.resourceFamilyName"
                placeholder="dddddd"
                name="resourceFamilyName"
              ></a-input>
            </a-form-model-item>
            <!-- 版本 -->
            <a-row class="special-row">
              <a-col :sm="10">
                <a-form-model-item label="" prop="revision">
                  <a-input
                    v-model:value="entity.revision"
                    placeholder="版本"
                    name="version"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :sm="14" class="special-col">
                <a-form-item label="">
                  <a-checkbox
                    name="isDefault"
                    :disabled="isRevOfRcdId"
                    v-model:checked="entity.isRevOfRcdId"
                    >默认版本</a-checkbox
                  >
                </a-form-item>
              </a-col>
            </a-row>
          </a-col>
          <a-col :sm="12">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="描述"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="测试">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>admin</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 最近修改时间 -->
              <a-form-model-item label="修改时间">
                <span> 2025-04-20 15:00</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GRdoPageEditBase } from "@/utils";

export default {
  name: "DataV2CreateOrEditCompRdo",
  mixins: [GRdoPageEditBase],
  data() {
    return {
      entity: {
        resourceFamilyName: "",
        description: "",
        isRevOfRcdId: false,
      },
      rules: {
        resourceFamilyName: [
          { required: true, message: "必填项", trigger: "change" },
        ],
        revision: [
            { required: true, trigger: 'change', message: '必填项' },
          ],
      },
    };
  },

  mounted() {},

  methods: {
    init() {
      console.log("init");
      this.loading = true;

      if (this.rdoId) {
        // 模拟请求
        setTimeout(() => {
          this.entity = {
            resourceFamilyName: "测试",
            description: "描述",
            isRevOfRcdId: this.rdoEditParams?.isRevOfRcdId,
            revision: 1.0,
          };
          this.loading = false;
        },500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceFamilyName);
          this.handleSaveEndBackView({
            id: 'ssss',
            rdoBaseId: 'ssss',
            rdoId: this.rdoId,
            name: 'result.name',
            version: this.entity.revision,
            isRevOfRcdId: this.entity.isRevOfRcdId,
          });
          this.refreshParent({
            reset: null,
          });
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.special-row {
  padding-top: 8px;
}

.special-col {
  padding-left: 8px;
}
</style>
